body {
    background-color: #F3767C;
}

.calculator {
    width: 310px;
    border-radius: 10px;
    margin: 50px auto;
    background-color: #39383B;
    padding: 20px;
}

.screen {
    width: 100%;
    background-color: #A2AF77;
    height: 60px;
    border-radius: 10px;
    margin-bottom: 10px;
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
}

.keyboard {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.key {
    background-color: #58595B;
    border: solid 1px #58595B;
    border-radius: 4px;
    color: white;
    width: 21%;
    height: 40px;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    font-size: 20px;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 25px;
    align-items: center;
    margin: 3px 6px;
}

.key:hover {
    background-color: #110D0F;
    border: solid 1px #110D0F;
}